.container-contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #9BC9C8;
    color: #FFFFFF;
    justify-content: center;

    padding: 10rem 0;

    font-size: 1.625rem;
    font-family: 'HewittBook';

    line-height: 2;


    .contact-title{
        font-size: 2.125rem;
        font-family: 'Hewitt';
        margin-bottom: 1.125rem;
    }

    .container-instagram{
        display: flex;
        flex-direction: row;
        img{
            margin-right: 0.5rem;
            color: #284482;
            width: 50px;
        }
        div+div{
            line-height: 2;
        }
    }

    .container-button{
        margin-top: 4.125rem;
        .button{
            width: 320px;
            color: #FFFFFF;
            background-color: #284482;
            border: none;
            font-size: 1.125rem;
            padding: 20px;
        }
    }
}

@media (max-width: 600px) {
    .container-contact{
        display: flex;
        padding: 5rem 0;
    }
}
@media (max-width: 375px) {
    .container-contact {
        display: flex;
        padding: 1rem;

        font-size: 1.2rem;

        .container-button{
            margin-top: 4.125rem;
            .button{
                width: 240px;
                color: #FFFFFF;
                background-color: #284482;
                border: none;
                font-size: 0.875rem;
            }
        }
    }

    
}